.featured {
  height: 100vh;
}
.featured .featured__vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured .featured__horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured .featured__horizontal .featured__name {
  font-size: 60px;
  font-weight: bold;
}
.featured .featured__horizontal .featured__info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}
.featured .featured__horizontal .featured__info div {
  display: inline-block;
  margin-right: 15px;
}
.featured .featured__horizontal .featured__info .featured__points {
  color: #46d369;
}
.featured .featured__horizontal .featured__description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}
.featured .featured__horizontal .featured__buttons {
  margin-top: 15px;
}
.featured .featured__horizontal .featured__buttons a {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  transition: all ease 0.3s;
}
.featured .featured__horizontal .featured__buttons a:first-child {
  background-color: white;
  color: #111;
}
.featured .featured__horizontal .featured__buttons a:not(:first-child) {
  color: white;
  background-color: #333;
}
.featured .featured__horizontal .featured__buttons a:hover {
  opacity: 0.7;
}
.featured .featured__horizontal .featured__genres {
  margin-top: 15px;
  color: #999;
  font-size: 18px;
}

@media (max-width: 760px) {
  .featured {
    height: 95vh;
  }
  .featured .featured__horizontal .featured__name {
    font-size: 40px;
  }
  .featured .featured__horizontal .featured__info {
    font-size: 16px;
  }
  .featured .featured__horizontal .featured__description {
    font-size: 14px;
    max-width: 60%;
  }
  .featured .featured__horizontal .featured__buttons a {
    font-size: 16px;
    padding: 10px 20px;
  }
  .featured .featured__horizontal .featured__genres {
    font-size: 14px;
  }
}/*# sourceMappingURL=FeaturedMovie.css.map */